/* Component containers
----------------------------------*/
.ui-widget {
	font-size: 1.1em;
}
.ui-widget .ui-widget {
	font-size: 1em;
}
.ui-widget input,
.ui-widget select,
.ui-widget textarea,
.ui-widget button {
	font-size: 1em;
}
.ui-widget-content {
	border-bottom: 1px solid #aaaaaa;
	//background: #ffffff url($imageUrl + "ui-bg_flat_75_ffffff_40x100.png") 50% 50% repeat-x;
	color: #222222;
}
.ui-widget-header {
	border-bottom: 1px solid #aaaaaa;
	//background: #cccccc url($imageUrl + "ui-bg_highlight-soft_75_cccccc_1x100.png") 50% 50% repeat-x;
